<script>

// Component
import feather from 'feather-icons';
import Loader from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';
import storageManager from '@/plugins/storage';
import moment from 'moment';
import FileManager from '@/interface/template/FileManager.vue';
moment().format();

export default {
    inject: ['apiServer'],
    data() {
      return {
        hostkey:this.$Key,
        collectionData: [],
        assetData: [],
        spaceData: [],
        collectionAdmin: [],
        dataItem: this.$route.params.id,
        loading_sources: true,
        activeTab: 'detail',
        tabs: [
        {
          code: "detail",
          name: "รายละเอียด",
          description: "Personal details and application.",
        },
        {
          code: "asset",
          name: "เว็บไซต์",
          description: "Asset details.",
        },
        {
          code: "admin",
          name: "ทีมงาน",
          description: "Admin settings.",
        },
        {
          code: "storage",
          name: "พื้นที่",
          description: "Storage information.",
        },
      ],
      }
    },
    components: {
      Loader,Subhead,FileManager
    },
    methods: {
      dateTime(value) {
        return moment(value).format("DD/MM/YYYY hh:mm:ss");
      },
      async getData() {
        if(storageManager.get('session','login')) {
          try {
            let item              = this.$route.params.id;
            this.loading_sources  = false;
            const resAPI          = await fetch("https://gateway.cloudrestfulapi.com/api/hostname/" + item,
            {
              method: 'GET',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
            });

            const collectionRes   = await resAPI.json();
            // console.log("collectionRes",collectionRes);

            if(resAPI.status === 200) {
              await this.getDBinfo();
              const adminRes           = await this.getAdminData(collectionRes)
              const spaceRes           = await this.getSpace(collectionRes)
              // console.log("adminRes",adminRes);

              if(adminRes) {

                const assetRes          = await this.getAssetsData()
                // console.log("assetRes",assetRes);

                if(assetRes) {
                  this.collectionAdmin  = adminRes;
                  this.assetData        = assetRes;
                  this.collectionData   = collectionRes;
                  this.spaceData        = spaceRes;
                  this.loading_sources  = true;
                }
              }
            }
            this.loading_sources = true;

          } catch (error) {
            console.log(error)
          }
        }
      },
      async getSpace(item) {
        if(storageManager.get('session','login')) {
          try {
            this.loading_sources  = false;
            const resAPI          = await fetch("https://gateway.cloudrestfulapi.com/api/space/" + item.spaceId,
            {
              method: 'GET',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
            });

            const spaceRes   = await resAPI.json();
            return spaceRes
          } catch (error) {
            console.log(error)
          }
        }
      },
      async getAdminData(collection) {
        if(storageManager.get('session','login')) {
          try {

            let item  = this.$route.params.id;

            const resAPI   = await fetch("https://gateway.cloudrestfulapi.com/api/user/query", {
                method: 'POST',
                headers: {'Content-Type': 'application/json','client-token-key':collection.key},
                body: JSON.stringify({
                    method: "find",
                    args: [
                    {
                      "collection": {
                        $in: [item]
                      }
                    }
                  ]
                })
            });

            const adminRes   = await resAPI.json();
            // console.log("getAdminData",adminRes);
            
            return adminRes

          } catch (error) {
            console.log(error)
          }
        }
      },
      async getAssetsData() {
        if(storageManager.get('session','login')) {
          try {

            let item  = this.$route.params.id;

            const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/hostname/query", {
              method: 'POST',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
              body: JSON.stringify({
                method: 'find',
                args: [
                  {
                    $and: [
                      { siteType: 'asset' },
                      { parent: item }
                    ]
                  }
                ]
              })
            });

            const assetRes   = await resAPI.json();
            
            return assetRes

          } catch (error) {
            console.log(error)
          }
        }
      },
      async getDBinfo() {
        if(storageManager.get('session','login')) {
          try {
            this.loading_sources  = false;
            const resAPI          = await fetch("https://gateway.cloudrestfulapi.com/api/db-info/",
            {
              method: 'GET',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
            });

            const collectionRes   = await resAPI.json();

            if(resAPI.status === 200) {
              console.log("dbinfo",collectionRes);
            }
            this.loading_sources = true;

          } catch (error) {
            console.log(error)
          }
        }
      },
      async deleteData(id) {
        if(storageManager.get('session','login')) {
          try {
            this.loading_sources = false;
            let accessToken       = storageManager.get('session','token')
            let item              = this.$route.params.id;
            const resAPI          = await fetch("https://asia-southeast2-elearning-6871c.cloudfunctions.net/schools/removeAdmin/" + item,
            {
              method: 'POST',
              headers: {'Content-Type': 'application/json','Authorization': 'Bearer ' + accessToken},
              body: JSON.stringify({
                userId: id,
              })
            });

            console.log(await resAPI);

            this.loading_sources = true;
            await this.getData();

          } catch (error) {
            console.log(error)
          }
        }
      },
      updated() {
        feather.replace();
      },
    },
    async mounted () {
      try {
        await this.getData();
      } catch (error) {
        console.log(Error);
      }
    },
    setup() {
      //console.log("Setup");
    },
};
</script>

